import React, { useState } from 'react'
import './style.css'
import {
    Routes,
    Route,
    NavLink,
    useNavigate
} from "react-router-dom";
import { SendToServer, LoadController } from '../Main.js'

class LoginPage extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
        };
        LoadController("LoginPage")
    }

    handleChange(event) {
        this.setState({ value: event.target.value });
    }

    componentWillUnmount() {
        console.log("Login page Unmount")
    }

    render() {

        return <div>
            <Routes>
                <Route exact path="/" element={<LoginForm />} />
                <Route path="/registration" element={<RegistrationForm />} />
                <Route path="/*" element={<A404 />} />
            </Routes>
        </div>;
    }
}
function A404() {
    return <div>404: Page not found</div>
}

function CopyrightYear() {
    return new Date().getFullYear();
}
function RegistrationForm() {
    let navigate = useNavigate();

    const [email, setEmail] = useState(0);
    const [password, setPassword] = useState(0);
    const [password2, setPassword2] = useState(0);
    const [companyName, setCompanyName] = useState(0);
    const [firstLine, setFirstLine] = useState(0);
    const [secondLine, setSecondLine] = useState(0);
    const [county, setCounty] = useState(0);
    const [country, setCountry] = useState(0);
    const [vAT, setVAT] = useState(0);
    const [license, setLicense] = useState(0);
    const [tAC, setTAC] = useState(0);
    const [Disabled, setDisabled] = useState(0);
    const [Errors, setErrors] = useState(0);

    function Click() {
        if (email !== "" && password !== "" && password === password2 && companyName !== "" && firstLine !== "" && license !== "" && tAC === true)
            return;

            var data = {
            CompanyName: companyName,
            Email: email,
            Password: password,
            FirstLine: firstLine,
            SecondLine: secondLine,
            County: county,
            Country: country,
            VAT_Number: vAT,
            License: license,
        }

        SendToServer("Registration", data, (data) => {
            setDisabled(false);
            if (data.Data === "success") {
                navigate("/Dashboard", { replace: true });

            } else {
                AddError(data.Data);
            }
        })
    }
    function AddError(message) {
        var rows = Errors;
        if(rows === undefined)
            rows = [];
        for (var i = 0; i < rows.lenght; i++) {
            rows.push(<div className="alert alert-danger alert-dismissible" role="alert"> {message} <button type="button" className="btn-close" data-bs-dismiss="alert" aria-label="Close"></button></div>);
        }
        setErrors(rows)
    }


    return <div>
        <section className="vh-100 custom3">
            <div className="container h-100">
                <div className="row d-flex justify-content-center align-items-center h-100">
                    <div className="col-lg-12 col-xl-11">
                        <div className="card text-black custom4">
                            <div className="card-body p-md-5">
                                <div className="row justify-content-center">
                                    <div className="col-md-10 col-lg-6 col-xl-5 order-2 order-lg-1">

                                        <p className="text-center h1 fw-bold mb-5 mx-1 mx-md-4 mt-4">Sign up</p>

                                        <form className="mx-1 mx-md-4">
                                        <div id="liveAlertPlaceholder">{Errors}</div>

                                            <div className="d-flex flex-row align-items-center mb-4">
                                                <i className="fas fa-envelope fa-lg me-3 fa-fw"></i>
                                                <div className="form-outline flex-fill mb-0">
                                                    <input type="email" id="Email" className="form-control" onChange={(event) => { setEmail(event.target.value); }} />
                                                    <label className="form-label" htmlFor="Email">Your Email</label>
                                                </div>
                                            </div>

                                            <div className="d-flex flex-row align-items-center mb-4">
                                                <i className="fas fa-lock fa-lg me-3 fa-fw"></i>
                                                <div className="form-outline flex-fill mb-0">
                                                    <input type="password" id="Password" className="form-control" onChange={(event) => { setPassword(event.target.value); }} />
                                                    <label className="form-label" htmlFor="Password">Password</label>
                                                </div>
                                            </div>

                                            <div className="d-flex flex-row align-items-center mb-4">
                                                <i className="fas fa-key fa-lg me-3 fa-fw"></i>
                                                <div className="form-outline flex-fill mb-0">
                                                    <input type="password" id="Password2" className="form-control" onChange={(event) => { setPassword2(event.target.value); }} />
                                                    <label className="form-label" htmlFor="Password2">Repeat your password</label>
                                                </div>
                                            </div>

                                            <div className="d-flex flex-row align-items-center mb-4">
                                                <i className="fas fa-address-card fa-lg me-3 fa-fw"></i>
                                                <div className="form-outline flex-fill mb-0">
                                                    <input type="text" id="CompanyName" className="form-control" onChange={(event) => { setCompanyName(event.target.value); }} />
                                                    <label className="form-label" htmlFor="CompanyName">Company Name</label>
                                                </div>
                                            </div>


                                            <div className="d-flex flex-row align-items-center mb-4">
                                                <i className="fas fa-address-card fa-lg me-3 fa-fw"></i>
                                                <div className="form-outline flex-fill mb-0">
                                                    <input type="text" id="FirstLine" className="form-control" onChange={(event) => { setFirstLine(event.target.value); }} />
                                                    <label className="form-label" htmlFor="FirstLine">Address Line 1</label>
                                                </div>
                                            </div>

                                            <div className="d-flex flex-row align-items-center mb-4">
                                                <i className="fas fa-address-card fa-lg me-3 fa-fw"></i>
                                                <div className="form-outline flex-fill mb-0">
                                                    <input type="text" id="SecondLine" className="form-control" onChange={(event) => { setSecondLine(event.target.value); }} />
                                                    <label className="form-label" htmlFor="SecondLine">Address Line 2</label>
                                                </div>
                                            </div>

                                            <div className="d-flex flex-row align-items-center mb-4">
                                                <i className="fas fa-address-card fa-lg me-3 fa-fw"></i>
                                                <div className="form-outline flex-fill mb-0">
                                                    <input type="text" id="County" className="form-control" onChange={(event) => { setCounty(event.target.value); }} />
                                                    <label className="form-label" htmlFor="County">County</label>
                                                </div>
                                            </div>

                                            <div className="d-flex flex-row align-items-center mb-4">
                                                <i className="fas fa-address-card fa-lg me-3 fa-fw"></i>
                                                <div className="form-outline flex-fill mb-0">
                                                    <input type="text" id="Country" className="form-control" onChange={(event) => { setCountry(event.target.value); }} />
                                                    <label className="form-label" htmlFor="Country">Country</label>
                                                </div>
                                            </div>

                                            <div className="d-flex flex-row align-items-center mb-4">
                                                <i className="fas fa-address-card fa-lg me-3 fa-fw"></i>
                                                <div className="form-outline flex-fill mb-0">
                                                    <input type="text" id="VAT" className="form-control" onChange={(event) => { setVAT(event.target.value); }} />
                                                    <label className="form-label" htmlFor="VAT">VAT Number</label>
                                                </div>
                                            </div>

                                            <div className="d-flex flex-row align-items-center mb-4">
                                                <i className="fas fa-address-card fa-lg me-3 fa-fw"></i>
                                                <div className="form-outline flex-fill mb-0">
                                                    <input type="text" id="License" className="form-control" onChange={(event) => { setLicense(event.target.value); }} />
                                                    <label className="form-label" htmlFor="License">License</label>
                                                </div>
                                            </div>


                                            <div className="form-check d-flex justify-content-center mb-5">
                                                <input
                                                    className="form-check-input me-2"
                                                    type="checkbox"
                                                    value=""
                                                    id="form2Example3c"
                                                    onChange={(event) => { setTAC(event.target.value); }}
                                                />
                                                <label className="form-check-label" htmlFor="form2Example3">
                                                    I agree all statements in <a href="#!">Terms of service</a>
                                                </label>
                                            </div>

                                            <div className="d-flex justify-content-center mx-4 mb-3 mb-lg-4">
                                                <button type="button" disabled={Disabled} className="btn btn-primary btn-lg" onClick={Click}>Register</button>
                                            </div>

                                        </form>

                                    </div>
                                    <div className="col-md-10 col-lg-6 col-xl-7 d-flex align-items-center order-1 order-lg-2">

                                        <img src="https://mdbcdn.b-cdn.net/img/Photos/new-templates/bootstrap-registration/draw1.webp" className="img-fluid" alt="Sample" />

                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    </div>
}
function LoginForm() {
    let navigate = useNavigate();
    const [email, setEmail] = useState(0);
    const [password, setPassword] = useState(0);
    const [Disabled, setDisabled] = useState(0);
    const [Errors, setErrors] = useState(0);

    function isValidEmailAddress(address) {
        return !!address.match(/.+@.+/);
    }

    async function handleSubmit(e) {
        e.preventDefault();
        setDisabled(true);
        if (isValidEmailAddress(email) && password !== "") {
            SendToServer("Login", { "Username": email, "Password": password }, (data) => {
                setDisabled(false);
                console.log();
                if (data.Data === "success") {
                    navigate("/Dashboard", { replace: true });

                } else {
                    AddError("Incorrect Username or Password")
                }
            })
        }
    }
    function AddError(message) {
        var rows = Errors;
        if(rows === undefined)
            rows = [];
        for (var i = 0; i < rows.lenght; i++) {
            rows.push(<div className="alert alert-danger alert-dismissible" role="alert"> {message} <button type="button" className="btn-close" data-bs-dismiss="alert" aria-label="Close"></button></div>);
        }
        setErrors(rows)
    }


    return <div>
        <div className="d-flex align-items-center justify-content-center custom1">
            <div>

                <div id="liveAlertPlaceholder">{Errors}</div>
                <section>
                    <div className="container-fluid align-self-center">
                        <div className="row d-flex justify-content-center align-items-center">
                            <div className="col-md-9 col-lg-6 col-xl-5">
                                <img src="https://mdbcdn.b-cdn.net/img/Photos/new-templates/bootstrap-login-form/draw2.webp"
                                    className="img-fluid" alt="Sample" />
                            </div>
                            <div className="col-md-8 col-lg-6 col-xl-4 offset-xl-1">
                                <form onSubmit={handleSubmit}>
                                    <div className="form-outline mb-4">
                                        <input type="email" id="form3Example3" className="form-control form-control-lg"
                                            placeholder="Enter a valid email address" onChange={(event) => { setEmail(event.target.value); }} />
                                        <label className="form-label" htmlFor="form3Example3" autoComplete="off">Email address</label>
                                    </div>


                                    <div className="form-outline mb-3">
                                        <input type="password" id="form3Example4" onChange={(event) => { setPassword(event.target.value); }} className="form-control form-control-lg"
                                            placeholder="Enter password" />
                                        <label className="form-label" htmlFor="form3Example4" autoComplete="off">Password</label>
                                    </div>


                                    <div className="text-center text-lg-start mt-4 pt-2">
                                        <button disabled={Disabled} type="submit" className="btn btn-primary btn-lg custom2" onClick={handleSubmit}>Login</button>
                                        <p className="small fw-bold mt-2 pt-1 mb-0">Don't have an account? <NavLink to="/registration" className="link-danger">Register</NavLink></p>
                                    </div>
                                </form>
                            </div>
                        </div>
                    </div>
                    <div
                        className="d-flex flex-column flex-md-row text-center text-md-start justify-content-between py-4 px-4 px-xl-5 bg-primary  fixed-bottom">

                        <div className="text-white mb-3 mb-md-0">
                            Copyright © <CopyrightYear />. All rights reserved.
                        </div>


                    </div>
                </section>


            </div>
        </div>
        <div
            className="d-flex flex-column flex-md-row text-center text-md-start justify-content-between py-4 px-4 px-xl-5 bg-primary  fixed-bottom">

            <div className="text-white mb-3 mb-md-0">
                Copyright © <CopyrightYear />. All rights reserved.
            </div>



        </div>
    </div>
}

export default LoginPage;
